@tailwind base;
@tailwind components;
@tailwind utilities;

header ul li {
  @apply px-5
}

.bg-primary{
  background: rgb(85 81 221);

}

.bg-secondary{
  background: rgb(42, 71, 151);

}
.mobile-nav{
  @apply fixed top-10 left-0 bg-gray-600 w-full h-1/6;
}
.mobile-nav  li {
  @apply w-full h-full text-center border-b-2;
}

.proj-desc {
  @apply absolute left-0 right-0 bottom-[0px] top-0 bg-secondary opacity-0 duration-500 hover:opacity-100;
}
.btn {
  @apply bg-primary px-5 py-2 font-bold hover:border-2 border-white rounded;
}

html {
  scroll-behavior: smooth;
}